
.worldbook-manager-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  padding: 20px;
}

.worldbook-manager-modal {
  background: #fff;
  border: 3px solid #000;
  max-width: 900px;
  width: 100%;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  font-family: '', serif;
}

.modal-header {
  background: #000;
  color: #fff;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 3px solid #000;

  h2 {
    font-family: '', serif;
    font-size: 24px;
    font-weight: bold;
    margin: 0;

    i {
      margin-right: 10px;
    }
  }

  .close-button {
    background: none;
    border: none;
    color: #fff;
    font-size: 32px;
    cursor: pointer;
    padding: 0;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background: rgba(255, 255, 255, 0.1);
    }
  }
}

.modal-content {
  padding: 20px;
  overflow-y: auto;
  flex: 1;
}

.settings-section {
  margin-bottom: 25px;
  padding-bottom: 25px;
  border-bottom: 2px solid #ddd;

  &:last-child {
    border-bottom: none;
  }

  h4 {
    font-family: '', serif;
    font-size: 20px;
    font-weight: bold;
    margin: 0 0 15px 0;
  }

  h5 {
    font-family: '', serif;
    font-size: 16px;
    font-weight: bold;
    margin: 15px 0 10px 0;
  }
}

.worldbook-selection {
  label {
    display: block;
    font-family: '', serif;
    font-weight: bold;
    margin-bottom: 8px;
  }
}

.worldbook-select-group {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;

  select {
    flex: 1;
    padding: 10px;
    border: 2px solid #000;
    background: #fff;
    font-family: '', serif;
    font-size: 16px;

    &:focus {
      outline: none;
      background: #f9f9f9;
    }
  }
}

.action-btn {
  padding: 10px 15px;
  border: 2px solid #000;
  background: #fff;
  font-family: '', serif;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;

  &:hover:not(:disabled) {
    background: #fff;
    transform: translateY(-2px);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &.small {
    padding: 5px 10px;
    font-size: 12px;
  }

  &.delete-btn {
    background: #d9534f;
    color: #fff;
    border-color: #d9534f;

    &:hover:not(:disabled) {
      background: #c9302c;
    }
  }

  &.refresh-btn {
    background: #5bc0de;
    color: #fff;
    border-color: #5bc0de;

    &:hover:not(:disabled) {
      background: #46b8da;
    }
  }

  &.cancel-btn {
    background: #777;
    color: #fff;
    border-color: #777;

    &:hover:not(:disabled) {
      background: #666;
    }
  }
}

.binding-info {
  margin-top: 15px;
  padding: 15px;
  background: #e7f3ff;
  border: 2px solid #4682b4;

  &.warning {
    background: #fff3cd;
    border-color: #ffc107;
  }

  .info-text {
    font-family: '', serif;
    margin: 0 0 10px 0;

    i {
      margin-right: 5px;
    }

    strong {
      color: #4682b4;
    }
  }

  .hint-text {
    font-family: '', serif;
    font-size: 14px;
    color: #666;
    margin: 0;

    i {
      margin-right: 5px;
    }
  }
}

.sync-info-box {
  background: #e7f3ff;
  border: 2px solid #4682b4;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 20px;
  display: flex;
  gap: 10px;
  align-items: flex-start;

  i {
    color: #4682b4;
    font-size: 20px;
    flex-shrink: 0;
    margin-top: 2px;
  }

  p {
    margin: 0;
    font-size: 14px;
    line-height: 1.6;
    color: #333;

    strong {
      color: #4682b4;
    }
  }
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  font-family: '', serif;
  cursor: pointer;

  input[type='checkbox'] {
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
}

.sync-strategy,
.entry-settings {
  margin-top: 15px;

  label {
    display: block;
    font-family: '', serif;
    font-weight: bold;
    margin: 10px 0 5px 0;
  }

  select,
  input {
    width: 100%;
    padding: 10px;
    border: 2px solid #000;
    background: #fff;
    font-family: '', serif;
    font-size: 16px;

    &:focus {
      outline: none;
      background: #f9f9f9;
    }
  }
}

.entries-list {
  max-height: 400px;
  overflow-y: auto;
  border: 2px solid #000;
  padding: 10px;
  background: #fafafa;
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #666;

  i {
    font-size: 48px;
    margin-bottom: 15px;
    display: block;
  }

  p {
    font-family: '', serif;
    font-size: 16px;
  }
}

.entry-item {
  background: #fff;
  border: 2px solid #000;
  padding: 15px;
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }
}

.entry-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;

  .entry-name {
    font-family: '', serif;
    font-weight: bold;
    font-size: 16px;
  }

  .entry-status {
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 12px;
    font-weight: bold;

    &.enabled {
      background: #5cb85c;
      color: #fff;
    }

    &.disabled {
      background: #d9534f;
      color: #fff;
    }
  }
}

.entry-content {
  font-family: '', serif;
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
  line-height: 1.6;
}

.entry-actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
}

.entry-count {
  font-size: 16px;
  color: #666;
  font-weight: normal;
}

.action-section {
  margin: 25px 0;
}

.major-action-btn {
  padding: 12px 24px;
  border: 3px solid #000;
  background: #fff;
  font-family: '', serif;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;

  i {
    margin-right: 8px;
  }

  &:hover:not(:disabled) {
    background: #fff;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &.danger-btn {
    background: #d9534f;
    color: #fff;
    border-color: #d9534f;

    &:hover:not(:disabled) {
      background: #c9302c;
    }
  }
}

.button-group {
  display: flex;
  gap: 15px;
  margin-top: 20px;

  button {
    flex: 1;
  }
}

.save-btn {
  padding: 12px 24px;
  border: 3px solid #000;
  background: #5cb85c;
  color: #fff;
  font-family: '', serif;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;

  i {
    margin-right: 8px;
  }

  &:hover {
    background: #4cae4c;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
}

.cancel-btn {
  padding: 12px 24px;
  border: 3px solid #000;
  background: #fff;
  font-family: '', serif;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: #fff;
    transform: translateY(-2px);
  }
}
