
// ====================  ====================
.image-library-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

// ====================  ====================
.image-library-modal {
  background: linear-gradient(135deg, #1a1a1a 0%, #0d0d0d 100%);
  border: 2px solid #fff;
  border-radius: 4px;
  width: 90vw;
  max-width: 1200px;
  height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 20px 60px rgba(255, 255, 255, 0.1);
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

// ====================  ====================
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  border-bottom: 2px solid #fff;
  background: rgba(255, 255, 255, 0.05);

  h2 {
    margin: 0;
    font-size: 24px;
    color: #fff;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  .close-btn {
    background: none;
    border: 2px solid #fff;
    color: #fff;
    font-size: 24px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    border-radius: 2px;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background: #fff;
      color: #000;
      transform: rotate(90deg);
    }
  }
}

// ====================  ====================
.toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  gap: 15px;
  flex-wrap: wrap;
  background: rgba(255, 255, 255, 0.02);

  .toolbar-left,
  .toolbar-right {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  .upload-btn {
    padding: 8px 16px;
    background: #fff;
    color: #000;
    border: 2px solid #fff;
    border-radius: 2px;
    cursor: pointer;
    font-weight: 600;
    font-size: 14px;
    transition: all 0.2s;
    white-space: nowrap;

    &:hover {
      background: #000;
      color: #fff;
      transform: translateY(-2px);
    }

    &.batch {
      background: transparent;
      color: #fff;

      &:hover {
        background: #fff;
        color: #000;
      }
    }
  }

  .category-filter {
    padding: 8px 12px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: #fff;
    border-radius: 2px;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background: rgba(255, 255, 255, 0.2);
      border-color: #fff;
    }

    option {
      background: #1a1a1a;
      color: #fff;
    }
  }

  .search-input {
    padding: 8px 16px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: #fff;
    border-radius: 2px;
    font-size: 14px;
    min-width: 200px;
    transition: all 0.2s;

    &::placeholder {
      color: rgba(255, 255, 255, 0.5);
    }

    &:focus {
      outline: none;
      background: rgba(255, 255, 255, 0.15);
      border-color: #fff;
    }
  }

  .stats {
    display: flex;
    align-items: center;
    gap: 8px;
    color: rgba(255, 255, 255, 0.7);
    font-size: 14px;
    font-weight: 600;

    .divider {
      color: rgba(255, 255, 255, 0.3);
    }

    span {
      white-space: nowrap;
    }
  }
}

// ====================  ====================
.upload-progress {
  padding: 15px 30px;
  background: rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);

  .progress-bar {
    height: 8px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 8px;

    .progress-fill {
      height: 100%;
      background: linear-gradient(90deg, #fff 0%, rgba(255, 255, 255, 0.8) 100%);
      transition: width 0.3s ease;
      animation: shimmer 1.5s infinite;
    }
  }

  p {
    margin: 0;
    color: #fff;
    font-size: 14px;
    text-align: center;
    font-weight: 600;
  }
}

@keyframes shimmer {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

// ====================  ====================
.image-grid {
  flex: 1;
  overflow-y: auto;
  padding: 20px 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 20px;
  align-content: start;

  // 
  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 5px;

    &:hover {
      background: rgba(255, 255, 255, 0.5);
    }
  }

  &.empty {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

// 
.empty-state {
  text-align: center;
  color: rgba(255, 255, 255, 0.5);

  .empty-icon {
    font-size: 80px;
    margin-bottom: 20px;
    opacity: 0.3;
  }

  p {
    margin: 8px 0;
    font-size: 18px;
    font-weight: 600;
  }

  .empty-hint {
    font-size: 14px;
    opacity: 0.7;
  }
}

// 
.image-card {
  position: relative;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    border-color: rgba(255, 255, 255, 0.5);
    transform: translateY(-4px);
    box-shadow: 0 10px 20px rgba(255, 255, 255, 0.1);

    .image-actions {
      opacity: 1;
    }
  }

  &.selected {
    border-color: #fff;
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);

    .selected-mark {
      opacity: 1;
    }
  }

  .selected-mark {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    background: #fff;
    color: #000;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 18px;
    opacity: 0;
    transition: opacity 0.2s;
    z-index: 2;
  }

  .image-thumbnail {
    width: 100%;
    height: 180px;
    overflow: hidden;
    background: rgba(255, 255, 255, 0.02);
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.3s;
    }
  }

  &:hover .image-thumbnail img {
    transform: scale(1.1);
  }

  .image-info {
    padding: 12px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);

    .image-name {
      margin: 0 0 6px 0;
      color: #fff;
      font-size: 14px;
      font-weight: 600;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .image-meta {
      margin: 0;
      color: rgba(255, 255, 255, 0.5);
      font-size: 12px;
    }
  }

  .image-actions {
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    gap: 6px;
    opacity: 0;
    transition: opacity 0.2s;

    .action-btn {
      width: 32px;
      height: 32px;
      background: rgba(0, 0, 0, 0.8);
      border: 1px solid #fff;
      border-radius: 2px;
      cursor: pointer;
      transition: all 0.2s;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;

      &:hover {
        background: #fff;
        transform: scale(1.1);
      }

      &.delete:hover {
        background: #ff4444;
        border-color: #ff4444;
      }
    }
  }
}

// ====================  ====================
.modal-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  border-top: 2px solid #fff;
  background: rgba(255, 255, 255, 0.05);

  .footer-right {
    display: flex;
    gap: 12px;
  }

  .footer-btn {
    padding: 10px 24px;
    border: 2px solid #fff;
    border-radius: 2px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
    text-transform: uppercase;
    letter-spacing: 1px;

    &.primary {
      background: #fff;
      color: #000;

      &:hover:not(:disabled) {
        background: #000;
        color: #fff;
        transform: translateY(-2px);
        box-shadow: 0 6px 12px rgba(255, 255, 255, 0.2);
      }

      &:disabled {
        opacity: 0.3;
        cursor: not-allowed;
      }
    }

    &.secondary {
      background: transparent;
      color: #fff;

      &:hover {
        background: rgba(255, 255, 255, 0.1);
      }
    }

    &.danger {
      background: transparent;
      color: #ff4444;
      border-color: #ff4444;

      &:hover {
        background: #ff4444;
        color: #fff;
      }
    }
  }
}

// ====================  ====================
@media (max-width: 768px) {
  .image-library-modal {
    width: 95vw;
    height: 90vh;
  }

  .toolbar {
    flex-direction: column;
    align-items: stretch;

    .toolbar-left,
    .toolbar-right {
      width: 100%;
      justify-content: space-between;
    }

    .search-input {
      min-width: auto;
      flex: 1;
    }
  }

  .image-grid {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 12px;
    padding: 15px;
  }

  .modal-footer {
    flex-direction: column;
    gap: 12px;

    .footer-right {
      width: 100%;

      .footer-btn {
        flex: 1;
      }
    }

    .footer-btn.danger {
      width: 100%;
    }
  }
}
