
.quest-manager-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  padding: 20px;
}

.quest-manager-panel {
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  background-color: #fff;
  border: 4px solid #000;
  border-radius: 0;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background-color: #000;
  border-bottom: 3px solid #000;

  h2 {
    margin: 0;
    font-size: 24px;
    font-weight: bold;
    color: #fff;

    i {
      margin-right: 10px;
      color: #fff;
    }
  }

  .close-btn {
    width: 36px;
    height: 36px;
    border: 2px solid #fff;
    background-color: #000;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0;
    transition: all 0.2s;

    &:hover {
      background-color: #fff;
      color: #000;
      border-color: #fff;
    }
  }
}

.panel-body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

// 
.quest-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  margin-bottom: 24px;

  .stat-item {
    padding: 16px;
    background-color: #fff;
    border: 3px solid #000;
    border-radius: 0;
    text-align: center;
    transition: all 0.2s;

    &:hover {
      background-color: #000;

      .stat-label {
        color: #fff;
      }

      .stat-value {
        color: #fff !important;
      }
    }

    .stat-label {
      display: block;
      font-size: 13px;
      color: #000;
      margin-bottom: 8px;
      font-weight: bold;
      text-transform: uppercase;
    }

    .stat-value {
      display: block;
      font-size: 32px;
      font-weight: bold;
      color: #000;
    }
  }
}

// 
.quest-tabs {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
  border-bottom: 2px solid #000;
  padding-bottom: 8px;

  .tab-btn {
    flex: 1;
    padding: 10px 16px;
    border: 3px solid #000;
    background-color: #fff;
    color: #000;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 0;
    transition: all 0.2s;
    text-transform: uppercase;

    &:hover {
      background-color: #e0e0e0;
    }

    &.active {
      background-color: #000;
      color: #fff;
      border-color: #000;
    }
  }
}

// 
.quest-list {
  .empty-state {
    text-align: center;
    padding: 60px 20px;
    color: #999;

    i {
      font-size: 64px;
      margin-bottom: 20px;
      opacity: 0.3;
    }

    p {
      font-size: 16px;
      margin: 0;
    }
  }

  .quest-card {
    padding: 16px;
    margin-bottom: 12px;
    background-color: #fff;
    border: 3px solid #000;
    border-radius: 0;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background-color: #f5f5f5;
      border-width: 4px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &.active-quest {
      border-left-width: 6px;
    }

    &.completed-quest {
      opacity: 0.6;
    }

    &.failed-quest {
      opacity: 0.5;
      text-decoration: line-through;
    }

    .quest-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;

      .quest-title {
        margin: 0;
        font-size: 16px;
        font-weight: bold;
        color: #000;
      }

      .quest-status {
        padding: 4px 12px;
        font-size: 11px;
        font-weight: bold;
        border-radius: 0;
        border: 2px solid #000;
        text-transform: uppercase;

        &.status-active {
          background-color: #000;
          color: #fff;
        }

        &.status-completed {
          background-color: #fff;
          color: #000;
        }

        &.status-failed {
          background-color: #666;
          color: #fff;
          border-color: #666;
        }
      }
    }

    .quest-description {
      margin: 0 0 8px 0;
      font-size: 14px;
      color: #555;
      line-height: 1.5;
    }

    .quest-progress {
      display: flex;
      align-items: center;
      font-size: 13px;
      color: #000;
      padding: 8px;
      background-color: #f5f5f5;
      border-left: 4px solid #000;
      border-radius: 0;

      .progress-label {
        font-weight: bold;
        margin-right: 8px;
      }

      .progress-text {
        flex: 1;
      }
    }
  }
}

// 
.quest-detail-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10001;
  padding: 20px;
}

.quest-detail-content {
  width: 100%;
  max-width: 600px;
  max-height: 80vh;
  background-color: #fff;
  border: 4px solid #000;
  border-radius: 0;
  display: flex;
  flex-direction: column;

  .detail-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 20px;
    background-color: #000;
    border-bottom: 3px solid #000;

    h2 {
      margin: 0;
      font-size: 20px;
      font-weight: bold;
      color: #fff;
    }

    .close-btn {
      width: 32px;
      height: 32px;
      border: 2px solid #fff;
      background-color: #000;
      color: #fff;
      font-size: 18px;
      font-weight: bold;
      cursor: pointer;
      border-radius: 0;
      transition: all 0.2s;

      &:hover {
        background-color: #fff;
        color: #000;
        border-color: #fff;
      }
    }
  }

  .detail-body {
    flex: 1;
    overflow-y: auto;
    padding: 20px;

    .detail-section {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }

      h3 {
        margin: 0 0 12px 0;
        font-size: 16px;
        font-weight: bold;
        color: #000;
        padding-bottom: 8px;
        border-bottom: 3px solid #000;
        text-transform: uppercase;
      }

      .detail-label {
        font-weight: bold;
        margin-right: 8px;
        color: #555;
      }

      .detail-text {
        margin: 0;
        font-size: 14px;
        line-height: 1.8;
        color: #333;
        white-space: pre-wrap;
      }
    }
  }
}
